import * as React from 'react';
import * as ReactDom from 'react-dom';
import { Version } from '@microsoft/sp-core-library';
import {
  BaseClientSideWebPart,
  IPropertyPaneConfiguration,
  PropertyPaneTextField
} from '@microsoft/sp-webpart-base';
import styles from './CustomTilesWebPart.module.scss';
import * as strings from 'CustomTilesViewWpWebPartStrings';
import CustomTilesViewWp from './components/CustomTilesViewWp';
import { ICustomTilesViewWpProps } from './components/ICustomTilesViewWpProps';

export interface ICustomTilesWebPartProps {
  description: string;
  siteurl: string;
}

export default class CustomTilesWebPart extends BaseClientSideWebPart<ICustomTilesWebPartProps> {

  public render(): void {
    const element: React.ReactElement<ICustomTilesViewWpProps > = React.createElement(
      CustomTilesViewWp,
      {
        siteurl: this.context.pageContext.site.absoluteUrl,
        spHttpClient: this.context.spHttpClient
      }
    );

    ReactDom.render(element, this.domElement);
  }

  protected get dataVersion(): Version {
    return Version.parse('1.0');
  }

  protected getPropertyPaneConfiguration(): IPropertyPaneConfiguration {
    return {
      pages: [
        {
          header: {
            description: strings.PropertyPaneDescription
          },
          groups: [
            {
              groupName: strings.BasicGroupName,
              groupFields: [
                PropertyPaneTextField('description', {
                  label: strings.DescriptionFieldLabel
                })
              ]
            }
          ]
        }
      ]
    };
  }
}